import React, {useContext} from 'react'
import noteContext from "../context/notes/noteContext"


const Noteitem = (props) => {
    const context = useContext(noteContext);
    const { deleteNote } = context;
    const { note, updateNote } = props;
    return (
        <div className="col-md-3   shadow-lg p-3 mb-5 bg-white rounded">
            <div className="card my-3">
                <div className="card-body">
                    <div className="d-flex align-items-center">
                        <h5 className="card-title nttitle ">{note.title}</h5>
                        <i className="far fa-trash-alt mx-4 edbtn" onClick={()=>{deleteNote(note._id);
                        props.showAlert("Deleted Sucessfully","success")}}></i>
                        <i className="far fa-edit " onClick={()=>{updateNote(note);}}></i>
                    </div>
                    <p className="card-text nttext">{note.description}</p>
                </div>
            </div>
        </div>
    )
}

export default Noteitem
