import React from 'react'
import { Link, useHistory, useLocation } from "react-router-dom";

  

const Navbar = () => {
    let history = useHistory();
    const logOut = () =>{
        localStorage.removeItem('token');
        history.push('/login');
    }
    let location = useLocation();
    return (
        <nav className="navbar nbcss navbar-expand-lg navbar-dark sticky-top">
            <div className="container-fluid">
                <Link className="navbar-brand nblogo" to="/">NOTE BOOK</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item mx-5">
                            <Link className={`nav-link ${location.pathname==="/"? "active": ""}`} aria-current="page" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${location.pathname==="/about"? "active": ""}`} to="/about">About</Link>
                        </li>

                    </ul>
                    {!localStorage.getItem('token')?
                    <form className="d-flex "> 
                    <Link className="btn csd btn-primary mx-5" to="/login" role="button">Login</Link>
                    <Link className="btn csd btn-primary mx-4" to="/signup" role="button">Signup</Link>
                    </form>:<button onClick={logOut} className='btn btn-primary'>Log Out</button>}
                </div>
            </div>
        </nav>
    )
}

export default Navbar
